.create,
.design {
  margin: 0;
}
.create {
  color: var(--color-skyblue);
}
.inspire {
  margin: 0;
  color: var(--color-mediumturquoise);
}
.design-create-inspire-container {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-53xl);
}
.welcome {
  align-self: stretch;
  position: relative;
  color: var(--color-white);
}
.my-projects {
  position: relative;
  line-height: 18px;
}
.button {
  border-radius: 50px;
  background-color: var(--color-white);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-5xl) var(--padding-29xl);
  cursor: pointer;
  color: var(--color-gray);
}
.text {
  position: absolute;
  top: 117px;
  left: 64px;
  width: 359px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.icon-twitter {
  position: absolute;
  height: 100%;
  width: 15.28%;
  top: 0;
  right: 84.72%;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  cursor: pointer;
}
.vector-icon,
.vector-icon1,
.vector-icon2,
.vector-icon3 {
  position: absolute;
  height: 98.62%;
  width: 12.28%;
  top: 1.15%;
  right: 63.88%;
  bottom: 0.23%;
  left: 23.84%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.vector-icon1,
.vector-icon2,
.vector-icon3 {
  height: 99.54%;
  width: 12.68%;
  top: 0.46%;
  right: 42.63%;
  bottom: 0;
  left: 44.69%;
}
.vector-icon2,
.vector-icon3 {
  width: 13.08%;
  right: 20.9%;
  left: 66.02%;
}
.vector-icon3 {
  height: 99.31%;
  width: 12.34%;
  top: 0.69%;
  right: 0;
  left: 87.66%;
}
.icon-twitter-parent {
  position: absolute;
  top: 1056px;
  left: 64px;
  width: 350.2px;
  height: 43.5px;
}
.frame-child {
  position: relative;
  border-radius: var(--br-21xl);
  width: 172px;
  height: 192px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.frame-parent,
.image-gallery {
  display: flex;
  justify-content: flex-start;
}
.frame-parent {
  flex: 1;
  flex-shrink: 0;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-5xs);
}
.image-gallery {
  position: absolute;
  top: 117px;
  left: 927px;
  width: 742px;
  flex-direction: row;
  align-items: center;
  gap: var(--gap-lg);
}
.div {
  position: relative;
  background-color: var(--color-darkslategray);
  width: 100%;
  height: 1185px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-pink);
  font-family: var(--font-poppins);
}
